import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ImageModalPage } from '../image-modal/image-modal.page';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';

@Component({
  selector: 'app-sumatra-west-details',
  templateUrl: './sumatra-west-details.page.html',
  styleUrls: ['./sumatra-west-details.page.scss'],
})
export class SumatraWestDetailsPage implements OnInit {

  sliderOpts = {
    zoom: false,
    slidesPerView: 1.5,
    centeredSlides: false,
    spaceBetween: 20
  };

  images = ['sumatra_west/batik/batik1', 'sumatra_west/batik/batik2', 'sumatra_west/batik/batik3']

  constructor(private modalController: ModalController,
    private screenOrientation: ScreenOrientation) {
    this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
  }

  openPreview(img) {
    this.modalController.create({
      component: ImageModalPage,
      componentProps: {
        img: img
      }
    }).then(modal => modal.present());
  }

  ngOnInit() {
  }

}
